'use strict';

var React = require('react');
var Venue = require('../Venue');
var StoreListener = require('../../mixins/StoreListener');
var ContextMixin = require('../../mixins/ContextMixin');

var { State} = require('react-router');

var List = React.createClass({
  mixins: [State, StoreListener, ContextMixin], //jshint ignore:line

  getStores(){
    return {
      venues: this.getFluxFromContext().getStore('venues')
    };
  },

  getStateFromStores(props) {//jshint ignore:line
    //var { q } = this.getParams();
    return {
      venues: this.getStores().venues.getVenues()
    };
  },

  componentDidMount() {
    this.repoDidChange(this.props);
  },

  repoDidChange() {
    this.getFluxFromContext().getActions('venues').searchVenue();
  },

  render() {
    var { venues } = this.state;
    if (venues.toJS().length === 0){
      return <div className="padding"><span>Loading...</span></div>;
    }
    return (
      <div className="padding">
        <ul className="table-view list">
          {this.state.venues.toJS().map((venue) =>
            <Venue key={venue.id} venue={venue} />
          )}
        </ul>
      </div>
    );
  }
});
module.exports = List;



