'use strict';

var { PropTypes } = require('react');

var ContextMixin = {
  contextTypes: {
        flux: PropTypes.object.isRequired
    },

    getFluxFromContext: function() {
      return this.context.flux;
    }
};

module.exports = ContextMixin;



