'use strict';

var { Flummox } =  require('flummox');
var VenuesActions = require('./actions/VenuesActions');
var VenuesStore = require('./stores/VenuesStore');

class Flux extends Flummox {
  constructor() {
    super();
    this.createActions('venues', VenuesActions);
    this.createStore('venues', VenuesStore, this);
  }
}

module.exports = Flux;



