'use strict';

var React = require('react');
var { PropTypes } = React;
var ImmutableRenderMixin = require('react-immutable-render-mixin');
var ContextMixin = require('../mixins/ContextMixin');

var VenueElement = React.createClass({

  mixins: [ImmutableRenderMixin, ContextMixin],

  propTypes: {
    venue: PropTypes.object.isRequired
  },

  render() {
    var { venue } = this.props;
    return (
      <div className="detail">
        <ul className="table-view">
          <li className="table-view-cell media list">
            <img className="media-object pull-left small-img" src={venue.attributes.photo} />
            <div className="media-body">
              <h2 className="now">{venue.attributes.name}@{venue.attributes.venue}</h2>
              <h3 className="now">{venue.attributes.date} - {venue.attributes.position}</h3>
            </div>
          </li>
        </ul>

        <div className="item">
          <img src={venue.attributes.photo} className="full-image" />
          <p>{venue.attributes.description}</p>
        </div>

        <div className="fav">
          <a onClick={this.handleAddToFavorites} >
            <span className="icon icon-star-filled"></span>
            <span className="tab-label">Add to Favs</span>
          </a>
        </div>
      </div>
    );
  },

  handleAddToFavorites () {
    this.getFluxFromContext().getActions('venues').addVenueToFavorites(this.props.venue.id);
  }

});

module.exports = VenueElement;



