'use strict';

var React = require('react');
var StoreListener = require('../../mixins/StoreListener');
var VenueElement = require('../VenueElement');
var ContextMixin = require('../../mixins/ContextMixin');

var { State} = require('react-router');

var Detail = React.createClass({
  mixins: [State, StoreListener, ContextMixin], //jshint ignore:line

  getStores(){
    return {
      venues: this.getFluxFromContext().getStore('venues')
    };
  },

  getStateFromStores(props) {//jshint ignore:line
    var { id } = this.getParams();
    return {
      venue: this.getStores().venues.getVenueById(id)
    };
  },

  render() {
    var { venue } = this.state;
    if (!venue) {
      return <div className="padding">Loading detail...</div>;
    }
    return (
      <div className="padding">
        <VenueElement venue={venue} />
      </div>
    );
  }
});

module.exports = Detail;



