'use strict';

var React = require('react');
var { PropTypes } = React;
var {Link} = require('react-router');
var ImmutableRenderMixin = require('react-immutable-render-mixin');

var Venue = React.createClass({

  mixins: [ImmutableRenderMixin],

  propTypes: {
    venue: PropTypes.object.isRequired
  },

  render() {
    var { venue } = this.props;
    return (
      <li className="table-view-cell media">
        <Link to="detail" params={{id: venue.id}}>
          <img className="media-object pull-left small-img" src={venue.attributes.photo} />
          <div className="media-body">
            <h2 className="now">{venue.attributes.name}@{venue.attributes.venue}</h2>
            <h3 className="now">{venue.attributes.date} - {venue.attributes.position}</h3>
            <p className="now">{venue.attributes.description.substring(0, 250) + '...'}</p>
          </div>
        </Link>
      </li>
    );
  }
});

module.exports = Venue;



