'use strict';

var React = require('react');
var Router = require('react-router');
var { Route, Handler, DefaultRoute } = Router;
var App = require('./App');
var List = require('./components/handlers/List');
var Detail = require('./components/handlers/Detail');
var Search = require('./components/handlers/Search');
var assign = require('object-assign');

var Parse = require('parse').Parse;
Parse.initialize('rRL8zzvGCZplFVSAL5eImlDm6ImLe3P74hN23uPu', 'LkVZh4mhxXBbcgms5bXoN6nKuGJYmL2Ck6rRvvHa');

var Flux = require('./Flux');
var flux = new Flux();

// Expose globally
window.React = React;

if ('addEventListener' in document) {
    document.addEventListener('DOMContentLoaded', function() {
        window.FastClick.attach(document.body);
    }, false);
}

var routes = (
  <Route name='explore' path='/' handler={App}>
    <Route name='list' path='/venues' handler={List} />
    <Route name='detail' path='/venues/:id' handler={Detail} />
    <DefaultRoute handler={Search} name="search"/>
  </Route>
);

Router.run(routes, function (Handler, state) {
  React.withContext({ flux }, function() {
    React.render(<Handler />, document.body);
  });
});



