'use strict';
var { Store } = require('flummox');
var Immutable = require('immutable');

class VenuesStore extends Store {
  constructor(flux) {
    super();
    var venuesActionIds = flux.getActionIds('venues');
    this.register(venuesActionIds.searchVenueSuccess, this.handleSearchVenueSuccess);
    this.register(venuesActionIds.addVenueToFavorites, this.addVenueToFavorites);
    this.state = {
      venues: Immutable.List(),
      venuesById: Immutable.Map()
    };
  }

  handleSearchVenueSuccess(result) {
    var venues = Immutable.fromJS(result);
    var venuesMap = venues.reduce((result, venue) => {
      result = result.set(venue.id, venue);
      return result;
    }, Immutable.Map());
    this.setState({
      venues: venues,
      venuesById: venuesMap
    });
  }

  getVenues() {
    return this.state.venues;
  }

  getVenueById(id) {
    return this.state.venuesById.get(id);
  }

  addVenueToFavorites(venueId) {
    alert(venueId);
  }
}

module.exports = VenuesStore;



