'use strict';

var { Actions } = require('flummox');
var Venue = require('../utils/Venue');

class VenuesActions extends Actions {
  searchVenue(){
    Venue.getVenues()
    .then(function(result){
      return this.searchVenueSuccess(result);
    }.bind(this)).
    catch(function(err){
      return this.searchVenueFailure(err);
    }.bind(this));
  }
  searchVenueSuccess(result){
    return result;
  }
  searchVenueFailure(err){
    console.log(err);
  }

  addVenueToFavorites(venueId){
    return venueId;
  }
}

module.exports = VenuesActions;



